import './Section.css';
import React from 'react';
import {Map, MapMarker, MarkerClusterer} from 'react-kakao-maps-sdk';
import axios from 'axios';
import { useAsync } from 'react-async';

function Section()
{
  const { 
    data: components,
    error,
    isLoading,
  } = useAsync({
    promiseFn: getToilets,
  });
  if (isLoading) return <div className="container">로딩중...</div>;
  if (error) return <div className="container">Error</div>;
  if(components)
  {
    console.log(components);
    const mapData = components.map(function(item){
      const a = {};
      a['lat'] = item.latitude;
      a['lng'] = item.longitude;
      return a;
    });
    console.log(mapData);
    return (  
      <div className="container mt-5 mb-5">
          <button type="button" className="btn btn-primary" style={{display:'block', marginLeft:'auto',marginRight:0}}>찾기</button>
          <Map
            center={{lat: 36.2683, lng: 127.6358 }}
            style={{width:"100%", height: "500px"}}
            level={14}>
            <MarkerClusterer
              averageCenter={true}
              minLevel={10}
            >
              { 
                mapData.map(function(item){
                  const idx = mapData.indexOf(item);

                  return(
                  <MapMarker
                    key={`${idx+1}`}
                    position={item}
                  />    )
  }) 
              }
            </MarkerClusterer>
            
            </Map>
      </div>
  );
  }
    
}

async function getToilets(){
  try
  {
    const endPoint = 'http://api.data.go.kr/openapi/tn_pubr_public_toilet_api';
    const response = await axios.get(endPoint + `?serviceKey=ylt4iE1vPMBHXot%2FVHC%2FqeZRw6jc%2F1gSwJlt4P%2FUtlnmAJhmmmMlH2a46vi4i4NMyaM%2Bfs2UC30vqPG1%2BgYT%2Bg%3D%3D&pageNo=0&numOfRows=1000&type=json`)
    .then(function(response){
      return response.data.response.body.items;
    });
    return response;
  } 
  catch(err)
  {

  }
}

export default Section;